@import url(https://fonts.googleapis.com/css2?family=Cabin&family=Roboto+Condensed:wght@400;700&display=swap);
*,
::after,
::before {
  box-sizing: border-box;
}

/* fonts */

html {
  font-size: 100%;
} /*16px*/

:root {
  /* colors */
  --primary-50: #e0fcff;
  --primary-100: #AAA4F4;
  --primary-200: #87eaf2;
  --primary-300: #54d1db;
  --primary-400: #38bec9;
  --primary-500: #847aef;
  --primary-600: #14919b;
  --primary-700: #5648EA;
  --primary-800: #0a6c74;
  --primary-900: #044e54;

  /* grey */
  --grey-50: #f0f4f8;
  --grey-100: #d9e2ec;
  --grey-200: #bcccdc;
  --grey-300: #9fb3c8;
  --grey-400: #829ab1;
  --grey-500: #627d98;
  --grey-600: #486581;
  --grey-700: #334e68;
  --grey-800: #243b53;
  --grey-900: #102a43;
  /* rest of the colors */
  --black: #222;
  --white: #fff;
  --red-light: #f8d7da;
  --red-dark: #842029;
  --green-light: #d1e7dd;
  --green-dark: #0f5132;

  /* fonts  */
  --headingFont: 'Roboto Condensed', Sans-Serif;
  --bodyFont: 'Cabin', Sans-Serif;
  --small-text: 0.875rem;
  --extra-small-text: 0.7em;
  /* rest of the vars */
  --backgroundColor: var(--grey-50);
  --textColor: var(--grey-900);
  --borderRadius: 0.25rem;
  --letterSpacing: 1px;
  --transition: 0.3s ease-in-out all;
  --max-width: 1120px;
  --fixed-width: 500px;
  --fluid-width: 90vw;
  --breakpoint-lg: 992px;
  --nav-height: 6rem;
  /* box shadow*/
  --shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-2: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-3: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-4: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

body {
  background: #E5ECF4;
  font-family: 'Cabin', Sans-Serif;
  font-family: var(--bodyFont);
  font-weight: 400;
  line-height: 1.75;
  color: #102a43;
  color: var(--textColor);
}

p {
  margin-bottom: 1.5rem;
  max-width: 40em;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-family: 'Roboto Condensed', Sans-Serif;
  font-family: var(--headingFont);
  font-weight: 400;
  line-height: 1.3;
  text-transform: capitalize;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text-small {
  font-size: 0.875rem;
  font-size: var(--small-text);
}

a {
  text-decoration: none;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}
a,
button {
  line-height: 1.15;
}
button:disabled {
  cursor: not-allowed;
}
ul {
  list-style-type: none;
  padding: 0;
}

.img {
  width: 100%;
  display: block;
  object-fit: cover;
}
/* buttons */

.btn {
  cursor: pointer;
  color: #fff;
  color: var(--white);;
  background: #847aef;;
  background: var(--primary-500);
  border: transparent;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
  padding: 0.375rem 0.75rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow: var(--shadow-2);
  transition: 0.3s ease-in-out all;
  transition: var(--transition);
  text-transform: capitalize;
  display: inline-block;
}
.btn:hover {
  background: #5648EA;
  background: var(--primary-700);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  box-shadow: var(--shadow-3);
}
.btn-hipster {
  color: #847aef;
  color: var(--primary-500);
  background: #87eaf2;
  background: var(--primary-200);
}
.btn-hipster:hover {
  color: #87eaf2;
  color: var(--primary-200);
  background: #5648EA;
  background: var(--primary-700);
}
.btn-block {
  width: 100%;
}
.btn-hero {
  font-size: 1.25rem;
  padding: 0.5rem 1.25rem;
}
.btn-danger {
  background: #f8d7da;
  background: var(--red-light);
  color: #842029;
  color: var(--red-dark);
}
.btn-danger:hover {
  background: #842029;
  background: var(--red-dark);
  color: #fff;
  color: var(--white);
}
/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin-bottom: 1rem;
  border-color: transparent;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  text-align: center;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}

.alert-danger {
  color: #842029;
  color: var(--red-dark);
  background: #f8d7da;
  background: var(--red-light);
}
.alert-success {
  color: #0f5132;
  color: var(--green-dark);
  background: #d1e7dd;
  background: var(--green-light);
}
/* form */

.form {
  width: 80vw;
  max-width: 500px;
  max-width: var(--fixed-width);
  background: #fff;
  background: var(--white);
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow: var(--shadow-2);
  padding: 2rem 2.5rem;
  margin: 3rem auto;
  transition: 0.3s ease-in-out all;
  transition: var(--transition);
}
.form:hover {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  box-shadow: var(--shadow-4);
}
.form-label {
  display: block;
  font-size: var(--smallText);
  margin-bottom: 0.5rem;
  text-transform: capitalize;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}
.form-input,
.form-textarea,
.form-select {
  width: 100%;
  padding: 0.375rem 0.75rem;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  background: #f0f4f8;
  background: var(--backgroundColor);
  border: 1px solid #bcccdc;
  border: 1px solid var(--grey-200);
}
.form-input,
.form-select,
.btn-block {
  height: 35px;
}
.form-row {
  margin-bottom: 1rem;
}

.form-textarea {
  height: 7rem;
}
::-webkit-input-placeholder {
  font-family: inherit;
  color: #829ab1;
  color: var(--grey-400);
}
::placeholder {
  font-family: inherit;
  color: #829ab1;
  color: var(--grey-400);
}
.form-alert {
  color: #842029;
  color: var(--red-dark);
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
  text-transform: capitalize;
}
/* alert */

@-webkit-keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loading {
  width: 6rem;
  height: 6rem;
  border: 5px solid #829ab1;
  border: 5px solid var(--grey-400);
  border-radius: 50%;
  border-top-color: #847aef;
  border-top-color: var(--primary-500);
  -webkit-animation: spinner 2s linear infinite;
          animation: spinner 2s linear infinite;
}
.loading-center {
  margin: 0 auto;
}
/* title */

.title {
  text-align: center;
}

.title-underline {
  background: #847aef;
  background: var(--primary-500);
  width: 7rem;
  height: 0.25rem;
  margin: 0 auto;
  margin-top: -1rem;
}

.container {
  width: 90vw;
  width: var(--fluid-width);
  max-width: 1120px;
  max-width: var(--max-width);
  margin: 0 auto;
}
.full-page {
  min-height: 100vh;
}

.coffee-info {
  text-align: center;
  text-transform: capitalize;
  margin-bottom: 1rem;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}
.coffee-info span {
  display: block;
}
.coffee-info a {
  color: #847aef;
  color: var(--primary-500);
}

@media screen and (min-width: 992px) {
  .coffee-info {
    text-align: left;
  }
  .coffee-info span {
    display: inline-block;
    margin-right: 0.5rem;
  }
}

.wall{
  background: url("https://i.ibb.co/9Y6dkNf/back.jpg");
  background-size: cover;
}

.tunnel {
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: black;
}

.scene{
  display: inline-block;
  vertical-align: middle;
  -webkit-perspective: 5px;
          perspective: 5px;
  -webkit-perspective-origin: 50% 50%;
          perspective-origin: 50% 50%;
  opacity: 0.4;
  top: 50%;
  left: 50%;
  position: absolute;
}

.wrap{
  position: absolute;
  width: 1000px;
  height: 1000px;
  left: -500px;
  top: -500px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  animation: move 12s infinite linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation: move 12s infinite linear;
}

.wrap:nth-child(2){
  animation: move 12s infinite linear;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
  -webkit-animation: move 12s infinite linear;
}

.homepage_div {
  background: rgba(113, 213, 215, 0.85);
  position: absolute;
  top: 33vh;
  padding: 1.5rem 4.5em;
  left: 40vw;
  width: 20em;
  box-shadow: #0099ff 0 0 30px -1px;
  border-radius: 3%;
  display: flex;
  justify-content: center;
}

.land_btn {
  background:#14919b;
}
.land_btn:hover {
  background:var(--primary-800);
}

@media (max-width: 1500px) {
  .homepage_div {
    width: 33%;
    top: 33vh;
    left: 31vw;
  }
}

@media (max-width: 1100px) {
  .homepage_div {
    width: 35%;
    left: 28vw;
    top: 36vh;
  }
}

@media (max-width: 900px) {
  .homepage_div {
    width: 44%;
    left: 28vw;
    top: 39vh;
  }
}

@media (max-width: 720px) {
  .homepage_div {
    width: 50%;
    left: 25vw;
    top: 39vh;
  }
}

@media (max-width: 576px) {
  .homepage_div {
    width: 57%;
    left: 20vw;
    top: 42vh;
  }
}

@media (max-width: 460px) {
  .homepage_div {
    width: 72%;
    left: 11vw;
    top: 42vh;
  }
}
.wall {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  animation: fade 12s infinite linear;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation: fade 12s infinite linear;
}

.wrap:nth-child(2) .wall {
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}

.wall-right {
  -webkit-transform: rotateY(90deg) translateZ(500px);
          transform: rotateY(90deg) translateZ(500px);
}

.wall-left {
  -webkit-transform: rotateY(-90deg) translateZ(500px);
          transform: rotateY(-90deg) translateZ(500px);
}

.wall-top {
  -webkit-transform: rotateX(90deg) translateZ(500px);
          transform: rotateX(90deg) translateZ(500px);
}

.wall-bottom {
  -webkit-transform: rotateX(-90deg) translateZ(500px);
          transform: rotateX(-90deg) translateZ(500px);
}

.wall-back {
  -webkit-transform: rotateX(180deg) translateZ(500px);
          transform: rotateX(180deg) translateZ(500px);
}

@-webkit-keyframes move {
  0%{
    -webkit-transform: translateZ(-500px) rotate(0deg);
            transform: translateZ(-500px) rotate(0deg);
  }
  100%{
    -webkit-transform: translateZ(500px) rotate(0deg);
            transform: translateZ(500px) rotate(0deg);
  }
}

@keyframes move {
  0%{
    -webkit-transform: translateZ(-500px) rotate(0deg);
            transform: translateZ(-500px) rotate(0deg);
  }
  100%{
    -webkit-transform: translateZ(500px) rotate(0deg);
            transform: translateZ(500px) rotate(0deg);
  }
}

@-webkit-keyframes fade {
  0%{
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}

@keyframes fade {
  0%{
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}
